.footer {
  background-color: orange;
  width: 100%;
  height: 361px;
}

.footer__top__content {
  width: 1170px;
  margin: 0 auto;
  padding: 48px 0;
  display: flex;
}

.footer__top__content__block {
  width: 210px;
  padding: 0 15px;
}

.footer__top__content__block__title {
  font-size: 12px;
  line-height: 18px;
  font-weight: 700;
  font-family: "Inconsolata", monospace;
  color: rgb(102, 102, 102);
}

.footer__top__link {
  text-decoration: none;
  color: white;
}

.footer__top__link:hover {
  text-decoration: underline;
}

.footer__top__content__block__text {
  font-family: "Inconsolata", monospace;
  font-size: 14px;
  line-height: 21px;
}

.footer__bot {
  border-top: 1px solid rgb(34, 34, 34);
}

.footer__bot__content {
  width: 1170px;
  margin: 0 auto;
  display: flex;
  padding: 18px 0 43px;
}

.footer__bot__content__left__lng {
  display: flex;
}

.lng__link {
  text-decoration: none;
  color: #666666;
}

.lng__link:hover {
  text-decoration: underline;
}

.footer__bot__content__left__lng__type {
  font-family: "Inconsolata", monospace;
  line-height: 21px;
  font-size: 14px;
  margin-right: 10px;
}

.lng__special__type {
  color: white;
}

.footer__bot__content__left__time__span {
  color: white;
}

.footer__bot__content__left__time {
  font-family: "Inconsolata", monospace;
  line-height: 21px;
  font-size: 14px;
  color: #666666;
  margin-top: 6px;
}

.span__link__users {
  text-decoration: none;
  color: #666666;
}

.span__link__users:hover {
  text-decoration: underline;
}

.footer__bot__content__left__users {
  font-family: "Inconsolata", monospace;
  color: #666666;
  font-size: 12px;
  line-height: 19px;
  margin-top: 12px;
}

.footer__bot__content__right {
  margin-left: auto;
}

.footer__bot__content__right__text {
  color: white;
  font-family: "Inconsolata", monospace;
  font-size: 12px;
  line-height: 21px;
}

.footer__bot__content__right__img {
  width: 37px;
  height: 20px;
  float: right;
  margin-top: 3px;
}

@media screen and (min-width: 990px) and (max-width: 1326px) {
  .footer__top__content {
    width: 940px;
  }
  .footer__bot__content {
    width: 940px;
    padding: 18px 0 25px;
  }
}
@media screen and (min-width: 420px) and (max-width: 1023px) {
  .footer {
    height: 686px;
  }
  .footer__top__content {
    width: 390px;
    height: 345px;
    flex-wrap: wrap;
    gap: 20px;
  }
  .footer__top__content__block {
    width: 185px;
    padding: 0 0 0 0;
  }
  .footer__bot__content {
    width: 390px;
    padding: 18px 0 0;
    height: 225px;
  }
  .footer__bot__content__left__lng {
    display: flex;
    flex-wrap: wrap;
  }
}